* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.banner {
  width: 100%;
  height: 100vh;
  background: url("./img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.banner_content {
  display: flex;
  align-items: center;
  height: 100vh;
  color: #fff;
}

.container {
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;

}

.banner_text {
  width: 500px;
}

.banner_text h3 {
  font-size: 50px;
}

.banner_text h1 {
  text-transform: uppercase;
  font-size: 80px;
  line-height: 80px;
  font-weight: 900;
  margin-top: 20px;
}

.banner_text p {
  font-size: 25px;
  line-height: 35px;
  font-weight: 400;
  margin-top: 20px;
}

.navbar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.navbar_text {
  display: flex;
  align-items: center;
  height: 60px;
  width: 1200px;
  margin: 0 auto;
}

.logo {
  width: 280px;
  font-size: 24px;
  font-weight: 900;
  color: #fff;
}

.logo span {
  color: #ff0000;
  text-decoration: underline;
}

.navbar_ul {
  width: 100%;
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
}

.navbar_ul li a {
  text-decoration: none;
  color: #fff;
  padding: 0 15px;
  font-weight: 800;
  text-transform: uppercase;
}

.banner_btn {
  margin-top: 30px;
}

.btn {
  text-decoration: none;
  outline: none;
  border: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px;
  padding: 20px 40px;
}

.btn-smart {
  background-color: rgba(255, 69, 79);
}

.about {
  padding: 80px 0;
}

.p-25 {
  padding: 45px;
}

.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.col-6 {
  width: 50%;
}

.col-3 {
  width: 25%;
}

.col-4 {
  width: 33.33333333333333%;
}

.about h3 {
  color: rgba(37, 37, 37, 0.7);
  font-size: 30px;
  margin-bottom: 12px;
  font-weight: 700;
}

.about h1 {
  font-size: 40px;
  color: #252525;
  font-weight: 700;
  line-height: 40px;
  margin-bottom: 15px;
}

.about p {
  font-size: 18px;
  color: #8b8b8b;
}

.about_btn {
  margin-top: 35px;
}

.about_img {
  width: 100%;
  overflow: hidden;
}

.about_img img {
  width: 100%;
  height: 335px;
}

.shop {
  width: 100%;
  background: url(./img/img-01.jpg);
  height: 450px;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.shop_text {
  height: 450px;
  display: flex;
  align-items: center;
  width: 400px;
  color: #fff;
  justify-content: center;
  margin: 0 auto;
}

.shop_text h1 {
  font-size: 40px;
  line-height: 60px;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.shop_btn {
  margin-top: 35px;
}

.clints {
  width: 100%;
  padding: 100px 0;
  background: url(../src/img/bg_1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  background-attachment: fixed;
}

.clint {
  text-align: center;
}

.clint h1 {
  font-size: 35px;
}

.clint p {
  font-size: 18px;
  font-weight: 500;
  margin-top: 10px;
}

.prices {
  width: 100%;
  padding: 80px 0;
}

.price {
  padding: 20px;
  text-align: center;
}

.price_img {
  width: 100%;
}

.price_img img {
  width: 250px;
}

.price_heading {
  margin-top: 20px;
  font-size: 25px;
}

.price_text {
  margin-top: 15px;
  color: #888888;
  font-size: 18px;
  font-weight: 300;
}

.price_rs {
  font-size: 30px;
  margin-top: 15px;
}

@media (max-width: 700px) {

  .navbar_text {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: auto;
    width: 100%;
    margin: 0 auto;
  }

  .navbar_ul {
    justify-content: center;
  }

  .logo {
    margin-bottom: 20px;
    margin-top: 20px;
  }

  .container {
    width: 100%;
    padding: 0 30px;
  }

  .banner_text {
    width: auto;
  }

  .banner_text h3 {
    font-size: 25px !important;
  }

  .banner_text h1 {
    text-transform: uppercase;
    font-size: 50px;
    line-height: 80px;
    font-weight: 900;
    margin-top: 20px;
  }
              
  .banner_text p {
    font-size: 20px;
    line-height: 35px;
    font-weight: 400;
    margin-top: 20px;
  }

  .col-3,
  .col-4,
  .col-6 {
    width: 100%;
  }
}